<template>
  <div>
    <navPatient/>
    <div class="container bg-light">
      <b-modal id="my-modal" ref="myModalRef" hide-footer title="Eliminar Paciente">
        <div class="container">
          <div class="row">
            <p>¿Está seguro que desea eliminar al paciente?</p>
            <div class="col-md-6 pl-0">
              <a
                href="#"
                @click="deleteById(selectedID)"
                class="btn btn-secondary btn-sm btn-block"
              >Confirmar</a>
            </div>
            <div class="col-md-6 pr-0">
              <a
                href="#"
                @click="$refs.myModalRef.hide()"
                class="btn btn-tertiary btn-sm btn-block"
              >Cancelar</a>
            </div>
          </div>
        </div>
      </b-modal>

      <h1 class="p-5 text-dark text-center">
        <b>Listado de Pacientes</b>
      </h1>
      <!-- User Interface controls -->
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Buscar" class="mb-0">
            <b-input-group>
              <b-form-input v-model="filter" placeholder="Escriba para buscar"></b-form-input>
              <b-input-group-append>
                <b-button :disable="!filter" @click="filter=''">Limpiar</b-button>
              </b-input-group-append>
            </b-input-group>
          </b-form-group>
        </b-col>

        <b-col md="6" class="my-1">
          <b-form-group label-cols-sm="3" label="Ordenar por" class="mb-0">
            <b-input-group>
              <b-form-select v-model="sortBy" :options="sortOptions">
                <option slot="first" :value="null">-- Seleccione --</option>
              </b-form-select>
              <b-form-select v-model="sortDesc" :disabled="!sortBy" slot="append">
                <option :value="false">Asc</option>
                <option :value="true">Desc</option>
              </b-form-select>
            </b-input-group>
          </b-form-group>
        </b-col>
      </b-row>

      <!-- Main table element -->
      <b-table
        id="table-patient"
        show-empty
        small
        hover
        stacked="xl"
        fixed
        empty-filtered-text="No se encontrarón registros en su búsqueda"
        empty-text="No hay datos para mostrar"
        :items="allPatients"
        :current-page="currentPage"
        :fields="columns"
        :filter="filter"
        :per-page="10"
        :sort-by.sync="sortBy"
        :sort-desc.sync="sortDesc"
        :sort-direction="sortDirection"
        @filtered="onFiltered"
      >
        <template slot="tipoDocumento" slot-scope="data">{{data.value.descripcion}}</template>
        <template slot="nombres" slot-scope="data">{{data.item.nombre}} {{data.item.apellido}}</template>
        <template slot="medico" slot-scope="data">{{ data.value.nombre }} {{ data.value.apellido }}</template>

        <template slot="internment" slot-scope="data">
          <router-link
            class="btn btn-success"
            :to="`paciente/${data.item.id}/internaciones`"
            v-if="data.item.internaciones.length>0"
          >
            <i class="fa fa-procedures"></i> Ver
          </router-link>
        </template>

        <template slot="actions" slot-scope="data">
          <router-link :to="`/paciente/${data.item.id}/editar`" class="btn btn-warning mr-1">
            <i class="fa fa-user-edit"></i>
          </router-link>
          <button class="btn btn-danger" type="button" @click="getId(data.item.id)">
            <i class="fa fa-trash-alt mb-1"></i>
          </button>
        </template>
      </b-table>
      <b-row class="p-5">
        <b-col md="6" class="my-1">
          <b-pagination
            size="md"
            :total-rows="totalRows"
            v-model="currentPage"
            :per-page="10"
            @input="find(currentPage)"
          ></b-pagination>
        </b-col>
      </b-row>
    </div>
    <Footer/>
  </div>
</template>

<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navPatient from "@/components/patient/navPatient.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "pacientes",
  data() {
    return {
      columns: [
        {
          key: "tipoDocumento",
          label: "Tipo de Documento",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "numDocumento",
          label: "Numero de Documento"
        },
        {
          key: "nombres",
          label: "Nombres",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "domicilio",
          label: "Domicilio",
          sortable: true,
          sortDirection: "desc"
        },
        {
          key: "obraSocial",
          label: "Obra Social"
        },
        {
          key: "medico",
          label: "Medico Responsable"
        },
        {
          key: "internment",
          label: "Internación"
        },
        {
          key: "actions",
          label: "Acciones"
        }
      ],
      patients: [],
      allPatients: [],
      totalRows: null,
      currentPage: 1,
      sortBy: null,
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      selectedID: null
    };
  },
  methods: {
    async find(currentPage) {
      axios
        .get(`${URL}/api/paciente?page=${currentPage - 1}`)
        .then(response => {
          this.patients = response.data.content;
          this.totalRows = response.data.totalElements;
        })
        .catch(error => console.log(error));
    },
    findAll() {
      axios
        .get(`${URL}/api/pacientes`)
        .then(response => (this.allPatients = response.data))
        .catch(error => console.log("Error pacientes: " + error));
    },
    deleteById(id) {
      axios
        .delete(`${URL}/api/paciente/${id}`)
        .then(response => {
          this.$refs.myModalRef.hide();
          this.patients = this.patients.filter(patient => patient.id != id);
          bootbox.alert({
            title: "Eliminado Correctamente",
            message: response.data.mensaje,
            backdrop: true,
            locale: "es"
          });
        })
        .catch(error => {
          this.$refs.myModalRef.hide();
          bootbox.alert({
            title: "Error",
            message: error.response.data.mensaje,
            locale: "es",
            backdrop: true
          });
          console.log("Error: " + error.response.status + " Not Found");
        });
    },
    getId(id) {
      this.selectedID = id;
      this.$refs.myModalRef.show();
    },
    onFiltered(itemsFiltered) {
      this.totalRows = itemsFiltered.length;
      this.currentPage = 1;
    }
  },
  mounted(currentPage) {
    this.find(currentPage);
    this.findAll();
  },
  computed: {
    sortOptions() {
      return this.columns
        .filter(f => f.sortable)
        .map(f => {
          return {
            text: f.label,
            value: f.key
          };
        });
    }
  },
  components: {
    navPatient,
    Footer
  }
};
</script>

